<template>
    <div class="information">
        <div class="headimg">
            <a href="#" ><img src="#" alt="" ></a>
        </div>

        <div class="intro">
            <h1>魏攀</h1>
            <p>enjoy life</p>
            <br>
        </div>
            
        <div class="list-bar">
            <ul>
                <li v-for="(item,index) in list" :key="item.title" class="list-select">
                <router-link :class="{'list-select' :itemCheck==index}" :to="item.path" @click.native="tabClass(index)">{{item.title}}</router-link>
                </li>
            </ul>
        </div> 
    </div>
        
</template>

<script>
export default {
    name:"Header",
    data(){
        return {
            list:[{
                title:'home',
                path:"/index"
            },{
                title:'about',
                path:"/about"
            },{
                title:'note',
                path:"/note"
            }],
            itemCheck:0
        }
    },
    components:{

    },
    methods:{
        tabClass(index){
            this.itemCheck = index ;
        }
    }
}
</script>

<style>
.information{
    display: block;
    float: left;
    width: 25%;
    background-color: gray;
}
.headimg{
    width: 100%;
    height: 300px;
    background-color: black;

}
.intro{
    text-align: center;
}
.intro h1{
    margin-top: 3rem;
    text-align: center;
}
.intro p{
    text-align: center;
    font-size: 1.2rem;
}
.list-bar{
    width: 100%;
}
.list-bar a{
    text-decoration: none;
}
.list-select :hover{
    color: brown;
}
.list-select{
    color: brown;
}
.list-bar ul{
    width: 100%;
    padding: 0px;
}
.list-bar li{
    list-style: none;
    text-align: center;
    margin-left: 10%;
    margin-top: 1.7rem;
    border-bottom: 1px solid black;
    padding-bottom: 0.4rem;
    width: 80%;
    font-size: 1.3rem;
}
</style>


